/* Stile di base */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Intestazione */
header {
  background-color: #007BFF;
  color: white;
  text-align: center;
  padding: 20px;
}

header h1 {
  font-size: 2rem;
  margin: 0;
}

/* Contenuto principale */
main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.cover {
  border-radius: 10px;
  width: 100%;
  max-width: 300px;
  height: auto;
  margin-bottom: 20px;
}

/* Stile moderno per titolo e artista */
.song-info h2,
.song-info p {
  font-size: 1.5rem; /* Dimensione uniforme */
  font-weight: bold; /* Grassetto */
  margin: 10px 0; /* Spaziatura uniforme */
  color: #2c3e50; /* Grigio scuro per un design moderno */
  text-align: center; /* Centra il testo */
  text-transform: capitalize; /* Iniziale maiuscola */
  line-height: 1.4; /* Migliora la leggibilità */
}

audio {
  margin-top: 20px;
  width: 100%;
  max-width: 300px;
}

/* Piè di pagina */
footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px;
  margin-top: auto;
}

footer p {
  margin: 0;
  font-size: 0.9rem;
}

/* Stile responsivo */
@media (max-width: 768px) {
  header h1 {
    font-size: 1.5rem;
  }

  .song-info h2,
  .song-info p {
    font-size: 1.2rem; /* Adatta dimensione per schermi piccoli */
  }
}
